<template>
  <ul class="app-explorer__header app-tabs">
    <li v-for="t in tabs" :key="t.key"
      :title="t.title"
      :class="t.classes"
      @click="switchTab(t.key)">
      <component :is="t.component" :data="t.payload" @userInput="searchHandler" />
    </li>
  </ul>
</template>

<script>
import AppExplorerSearch from './AppExplorerSearch';
import AppExplorerSvg from './AppExplorerSvg';

export default {
  name: 'app-explorer-header',
  components: {
    AppExplorerSearch,
    AppExplorerSvg,
  },
  props: {
    tab: String,
  },
  computed: {
    tabs() {
      return [
        {
          key: 'search',
          title: 'Search',
          classes: {
            'app-tabs__tab': true,
            'app-tabs__tab--search': true,
            'app-tabs__tab--active': this.tab === 'search',
          },
          component: 'app-explorer-search',
        },
        {
          key: 'browse',
          title: 'Browse',
          classes: {
            'app-tabs__tab': true,
            'app-tabs__tab--browse': true,
            'app-tabs__tab--active': this.tab === 'browse',
          },
          component: 'app-explorer-svg',
          payload: '<svg inline-svg=""  role="presentation" focusable="false" width="40" height="40" xmlns="http://www.w3.org/2000/svg"><g transform="translate(10 13)" fill="none" fill-rule="evenodd"><path fill="#333" d="M6 2h2v14H6zM12 2h2v14h-2z"/><rect stroke="#333" stroke-width="2" x="1" y="1" width="18" height="14" rx="2"/></g></svg>',
        },
        {
          key: 'recent',
          title: 'Recent places',
          classes: {
            'app-tabs__tab': true,
            'app-tabs__tab--recent': true,
            'app-tabs__tab--active': this.tab === 'recent',
          },
          component: 'app-explorer-svg',
          payload: '<svg inline-svg=""  role="presentation" focusable="false" width="40" height="40" xmlns="http://www.w3.org/2000/svg"><g fill="#333" fill-rule="evenodd"><path d="M13 21h4l-5 5-5-5h4c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10a9.969 9.969 0 0 1-7.071-2.929l1.414-1.414A8 8 0 1 0 13 21z"/><path fill-rule="nonzero" d="M22 16h-2v5.413l3.79 3.812 1.42-1.41L22 20.587z"/></g></svg>',
        },
      ];
    },
  },
  methods: {
    switchTab(tab) {
      this.$emit('selectTab', tab);
    },
    searchHandler(str) {
      this.$store.commit('setSearchText', str);
    },
  },
};
</script>

<style lang="scss">
.app-explorer__header {
  display: flex;
  align-items: stretch;
  box-sizing: content-box;
}
.app-tabs__tab {
  height: 2.75rem;
  width: 2.5rem;
  white-space: nowrap;
  overflow: hidden;
  border-bottom: .25rem solid transparent;
  display: flex;
  align-items: stretch;
  cursor: pointer;
  svg {
    width: 2.5rem;
    height: 2.5rem;
  }
  &:hover {
    background-color: #eee;
  }
  &--search {
    flex: 1;
  }
  &--active {
    border-color: #9c3;
  }
}
</style>



